.header {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.headerImg {
    width: 200px;
    cursor: pointer;
}

.popupWrapper {
    position: relative;
}

.popupWrapper:hover .popup {
    display: block;
}

.back {
    z-index: -1;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -30%);
    background-color: aqua;
    width: 200px;
    height: 100px;
}
