.popup {
    background-color: white;
    position: absolute;
    top: 100%;
    left: 50%;
    transform: translateX(-60%);
}

.popup__content {
    width: 200px;
    height: 330px;
    border: 1px solid black;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding: 20px;
    box-sizing: border-box;
}

.popup__content > * {
    margin-bottom: 30px;
}

.popup__content__contact {
    margin-top: 25px;
    margin-bottom: 10px;
    font-size: 10px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
}

.popup__content__contact span:first-child {
    margin-bottom: 10px;
}

.popup__content__insta {
    margin-bottom: 0px;
}

.popup__content__insta a {
    display: flex;
    align-items: center;
}

.popup__content__insta img {
    width: 30px;
    height: 30px;
}

.popup__content__insta img:first-child {
    width: 20px;
    height: 20px;
    margin-right: 10px;
    opacity: 0.5;
}

.popup__content__insta__text {
    font-size: 10px;
    opacity: 0.8;
    margin-right: 30px;
}
